<template>
  <div class="typed" :style="dynamicStyle">
    <span class="typed-strings"></span>
  </div>
</template>
<script setup lang="ts">
import { onMounted, ref } from 'vue'
// import Typed from 'typed.js' // 

const props = defineProps({
  fontSize: {
    type: Number,
    default: 36,
  },
  strings: {
    type: Array,
    default: [],
  },
  typeSpeed: {
    type: Number,
    default: 100,
  },
  textColor: {
    type: String,
    default: '#fff',
  },
})

const dynamicStyle = ref(
  `--fontSize:${props.fontSize}px;--textColor:${props.textColor}`
)

onMounted(() => {
  const typed = new Typed('.typed-strings', {
    strings: props?.strings as Array<string>,
    typeSpeed: props.typeSpeed, //
    smartBackspace: true, //  false
    backSpeed: 100, //
    backDelay: 500, //
    loop: true, // false
    startDelay: 1000, //
    fadeOut: true, //
  })
})
</script>
<style lang="scss" scoped>
.typed {
  width: 100%;
  color: var(--textColor);
  font-size: var(--fontSize);
  font-weight: bold;
  text-align: center;
  margin-top: 10px;
  margin-bottom: 10px;
}
</style>
