export const helloInit = () => {
  const hour = new Date().getHours()
  let hello = null
  if (hour < 6) {
    hello = ''
  } else if (hour < 9) {
    hello = ''
  } else if (hour < 12) {
    hello = ''
  } else if (hour < 14) {
    hello = ''
  } else if (hour < 17) {
    hello = ''
  } else if (hour < 19) {
    hello = ''
  } else if (hour < 22) {
    hello = ''
  } else {
    hello = ''
  }
  ElMessage({
    dangerouslyUseHTMLString: true,
    message: `<strong>${hello}</strong> `,
  })
}

// 
const anniversaries = {
  4.4: '',
  5.12: '',
  7.7: '',
  9.18: '',
  12.13: '',
} as any
export const checkDays = (isBaseDay: boolean = true) => {
  // 
  if (isBaseDay) {
    const myDate = new Date()
    const mon = myDate.getMonth() + 1
    const date = myDate.getDate()
    const key = `${mon}.${date}`
    if (anniversaries.hasOwnProperty(key)) {
      console.log(`${anniversaries[key]}`)
      const gray = document.createElement('style')
      gray.innerHTML = 'html{filter: grayscale(100%)}'
      document.head.appendChild(gray)
      ElMessage({
        message: `${anniversaries[key]}`,
        duration: 14000,
        icon: h(SpaCandle, { theme: 'filled', fill: '#efefef' }),
      })
    }
  } else {
    // 
    const gray = document.createElement('style')
    gray.innerHTML = 'html{filter: grayscale(100%)}'
    document.head.appendChild(gray)
  }
}

// 
export const siteDateStatistics = (startDate: any) => {
  const currentDate = new Date()
  const differenceInTime = currentDate.getTime() - startDate.getTime()
  const differenceInDays = differenceInTime / (1000 * 3600 * 24)
  const differenceInMonths = differenceInDays / 30
  const differenceInYears = differenceInMonths / 12
  if (differenceInYears >= 1) {
    return ` ${Math.floor(differenceInYears)}  ${Math.floor(
      differenceInMonths % 12
    )}  ${Math.round(differenceInDays % 30)} `
  } else if (differenceInMonths >= 1) {
    return ` ${Math.floor(differenceInMonths)}  ${Math.round(
      differenceInDays % 30
    )} `
  } else {
    return ` ${Math.round(differenceInDays)} `
  }
}
