import { defineStore } from 'pinia'

export const storeKey = 'main'

export const mainStore = defineStore(storeKey, {
  state: () => {
    return {
      innerWidth: null, // 
      systemSettings: {
        showHomeBtn: true,
        showSingleStar: false,
        showLantern: false,
        showTimeAndWeather: true,
        showDarkMode: true,
        silentMode: false,
        seasonMode: 'default',
        musicSource: 'tencent',
        pcBgc: '/images/pc/1.webp',
        mobileBgc: '/images/mobile/1.jpg',
      } as SystemSettings,
      isDark: false, // ,
      themeTextColor: '#fff',
      siteStartShow: true, // 
      musicIsOk: false, // 
      musicVolume: 0, // ;
      mobileOpenState: false, // 
      mobileFuncState: false, // 
      setOpenState: false, // 
      musicOpenState: false, // 
      musicName: null, // 
      musicArtist: null, // 
      musicLrc: '', // 
      musicClick: false, // 
    }
  },
  getters: {
    // 
    getMusicLrc(state) {
      return state.musicLrc
    },
    // 
    getPlayerData(state) {
      return {
        name: state.musicName,
        artist: state.musicArtist,
      }
    },
    getSystemSetting(state) {
      return state.systemSettings as SystemSettings
    },
    getIsDark(state) {
      return state.isDark
    },
    getInnerWidth(state) {
      if (state.innerWidth) {
        return state.innerWidth
      }
      return JSON.parse(localStorage.getItem(storeKey) + '')
    },
  },
  actions: {
    setInnerWidth(value: any) {
      this.innerWidth = value
      this.mobileOpenState = value >= 720 ? false : true
      if (value) {
        this.systemSettings.seasonMode = 'snow'
      }
    },
    setSystemSetting(value: SystemSettings) {
      this.systemSettings = Object.assign(
        {},
        { ...this.systemSettings },
        { ...value }
      )
    },
    setIsDark(value: boolean) {
      this.isDark = value
    },
    // 
    setPlayerState(value: any) {
      this.musicOpenState = value ? false : true
    },
    // 
    setMusicLrc(value: any) {
      this.musicLrc = value
    },
    // 
    setPlayerData(name: any, artist: any) {
      this.musicName = name
      this.musicArtist = artist
    },
  },
  //   persist: true, // 
  persist: {
    key: 'data',
    storage: window.localStorage, // 
    paths: ['seasonMode'],
  },
})
